
$(function () {
  function IETester(userAgent){
    var UA =  userAgent || navigator.userAgent;
    if(/msie/i.test(UA)){
        return UA.match(/msie (\d+\.\d+)/i)[1];
    }else if(~UA.toLowerCase().indexOf('trident') && ~UA.indexOf('rv')){
        return UA.match(/rv:(\d+\.\d+)/)[1];
    }
    return false;
  }
  if(IETester()&&IETester()<10){
    $('*').css('opacity','1')
  }

  $('#fullpage').fullpage({
    css3: true,
    anchors: ['homepage', 'energysave', 'innovation', 'partner', 'news'],
    menu: ".menu",
    afterLoad: function (anchorLink, index) {
      //首屏幕的动画
      if (!$('.homepage').hasClass('on')) {
        setTimeout(function () {
          $('.homepage').addClass('on')
        }, 2000)
      }
      toChange(index)
    },
    onLeave: function (index, direction) {
      // console.log('离开', index, direction)
    }
  });

  function toDecimal(x) {
    var f = parseFloat(x);
    if (isNaN(f)) {
      return false;
    }
    var f = Math.round(x * 10) / 10;
    var s = f.toString();
    var rs = s.indexOf('.');
    if (rs < 0) {
      rs = s.length;
    }
    return s;
  }
  //每屏首次加载后执行的函数
  function toChange(index) {
    switch (index) {
      case 2:
        if (!$('.energysave').hasClass('on')) {
          $('.energysave')
            .addClass('on')
            .find('li b').each(function (i, v) {
              $(this).prop('Counter', 0).animate({
                Counter: $(this).text()
              }, {
                duration: 2000,
                easing: 'swing',
                step: i == 2 ? function (now) {
                  $(this).text(toDecimal(now));
                } : function (now) {
                  $(this).text(Math.ceil(now));
                }
              });
            });
        }
        break;
      case 3:
        if (!$('.innovation').hasClass('on')) {
          $('.innovation').addClass('on')
        }
        break;
      case 4:
        if (!$('.partner').hasClass('on')) {
          $('.partner').addClass('on')
        }
        break;
      case 5:
        if (!$('.news').hasClass('on')) {
          $('.news').addClass('on')
        }
        break;
    }
  }
  //创新科技四个气泡框选择
  $('#fourbubble').on('click', 'li', function () {
    var index = $(this).index()
    $('#fourcontent span').animate({
      'left': 12.5 + 25 * index + '%'
    }, 100)
    $('#fourcontent p').eq(index).addClass('on').siblings('p').removeClass('on')
  })
  //新闻列表,设置轮播
  function getnews() { 
    $.ajax({
      url: 'http://test.oms.hwariot.com/api/v1/news/?page_size=4',
      type: 'GET',
      dataType: "json",
      data: {},
    }).done(function(res){
      if (res.count > 0) {
        var page = '';
        res.results.forEach(function(v, i){
          var a = v.summary.length > 40 ? "..." : "";
          page += 
          '<li>'+
            '<div class="imgbox">'+
              '<div>'+
                '<img src='+v.image_url+'>'+
              '</div>'+
            '</div>'+
            '<h3>'+v.title+'</h3>'+
            '<p>'+v.summary.substr(0, 40)+a+'</p>'+
            '<div>'+
              '<span class="date">'+v.created_time.replace('T','&nbsp;&nbsp;')+'</span>'+
              '<a href="/@/newsdetail.html?id='+v.id+'">详情 >></a>'+
            '</div>'+
          '</li>'
        })
        $('#newslist ul').html(page)

      }
    }).done(function(res){
      if ($(window).width() > 768) {
        carousel()
      }
      clipimg()
    })
  }
  getnews()

  //新闻列表轮播图
  function carousel() {
    var container = document.getElementById('newslist')
    var ul = container.querySelector('ul');
    var lis = ul.querySelectorAll('li');
    var timer = null;
    var index = 1;
    var containerWidth = container.offsetWidth;
    var liWidth = lis[0].offsetWidth;

    for (var i = 0; i < 2; i++) {
      var cloneLi = lis[i].cloneNode(true);
      ul.appendChild(cloneLi)
    }

    function setUlWidth() {
      [].forEach.call(ul.querySelectorAll('li'), function (v, i) {
        v.style.width = liWidth + 'px';
        ul.style.width = liWidth * 6 + 'px' //这里可以优化下性能，不过不是重点
      })
    }
    setUlWidth();

    timer = setInterval(function () {
      $(ul).animate({
        'marginLeft': -liWidth * index++
      }, 500)
      if (index === 5) {
        $(ul).css({
          'marginLeft': 0
        })
        index = 1
      }
    }, 8000)
  }
  //裁剪图片
  function clipimg() {
    $('#newslist img').each(function (i, v) {
      $(v.parentNode).css({
        'height': $(v).width() * 0.66
      })
    })
  }

  //图片微交互
  function shakeAnimation(dom) {
    $(dom).mouseenter(function () {
      var thisPX = $(this).offset().left;
      var thisPY = $(this).offset().top;
      var boxWidth = $(this).outerWidth();
      var boxHeight = $(this).outerHeight();
      $(this).mousemove(function (event) {
        var mouseX = event.pageX - thisPX;
        var mouseY = event.pageY - thisPY;
        var X;
        var Y;
        if (mouseX > boxWidth / 2) {
          X = mouseX - boxWidth / 2;
        } else {
          X = mouseX - boxWidth / 2;
        }
        if (mouseY > boxHeight / 2) {
          Y = boxHeight / 2 - mouseY;
        } else {
          Y = boxHeight / 2 - mouseY;
        }

        $(this).find('img').each((i, v) => {
          var s = i % 2 === 0 ? (10 + 10 * i) : (10 + 10 * i)
          $(v).css({
            "transform": "translateY(" + X / s + "px) " + "translateX(" + Y / s + "px) "
          })
        })
      });
    });
    $(dom).mouseleave(function () {
      $(this).css({
        // "transform": "rotateY(0deg) rotateX(0deg)"
      });
    });
  }
  shakeAnimation('aside')


  //resize
  var timer = null;
  $(window).on('resize', function () {
    clearTimeout(timer)
    timer = setTimeout(function () {
      // location.reload()
    }, 300)

  })
});

